
.t-content{
	margin-top:62px;
}
.weui-cells{margin-top: 0;
}
.weui-label{font-size: 15px;
}
.weui-cells:before{border-top:none
}
.weui-label{width: 6em!important;
}
.weui-cell__bd input::-webkit-input-placeholder{color: #999;font-size: 15px;
}
.weui-cell__bd .weui-select{font-size: 15px;
}
.weui-cell__bd option{color: #999;font-size: 15px;
}
.weui-textarea{height: 120px!important;
}
.weui-cells__title {margin-bottom: 0.77em;
}
.weui-cells_radio .weui-cell__ft{padding-left:0;width: 30px;height: 30px;border:1px solid #ccc;border-radius: 50%;
}
.weui-cells_radio .weui-check:checked + .weui-icon-checked:before{font-size: 21px;
}
.weui-btn_primary{background-color:#5998F5 ;
}
button.weui-btn{width: 94%;margin:10px auto;font-size: 15px;
}
.year_paper{margin-bottom: 75px;padding-left: 20px;padding-top: 10px;
}
.year_paper label{font-size: 13px;color:#999
}
.year_paper label input{position: relative;top:1px
}
.year_paper .agree{margin-right: 10px;
}
input{font-size: 15px;
}
.btn{position:fixed;bottom:0px;height: 55px;background-color: #fff;width: 100%;border-top:1px solid #ededed
}



