
.hide {
  display: none !important;
}
.icon-message {
  background: url('../../../static/img/icon-message.jpg');
  display: inline-block;
  width: 16px;
  height: 15px;
  background-size: 100% 100%;
}
.coll_footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 60px;
  background: #fff;
  border-top: 1px solid #eee;
  z-index: 100;
  box-sizing: border-box;
  padding: 0 0 0 15px;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.del_btn_wrap {
  text-align: right;
}
.coll_btn_del {
  width: 120px;
  height: 60px !important;
  color: #fff;
  border: none;
  background: #DCDCDC;
  display: inline-block;
}
.coll_btn_del.active {
  background: #FF4A3D;
}
.del_radio {
  margin-top: 15px;
  padding: 0 0px 10px 12px;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  box-sizing: border-box;
  overflow: hidden;
  border-bottom: 1px solid #E6E6E6;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
}
.del_radio .del_radio_wrap {
  margin-right: 10px;
  box-sizing: border-box;
  font-size: 15px;
  position: relative;
}
.del_radio .del_radio_wrap .radio {
  display: inline-block;
  width: 18px;
  height: 18px;
  border: 1px solid #9B9B9B;
  border-radius: 50%;
  box-sizing: border-box;
  position: relative;
}
.del_radio .del_radio_wrap .radio.active {
  background: url('../../../static/img/sp-xuanzhong@2x.png');
  background-size: 100% 100%;
  border: none;
}



