
.space_detail .weui-cells{margin-top: 0;
}
.space_detail .weui-label{font-size: 15px;
}
.space_detail .weui-cells:before{border-top:none
}
.space_detail .weui-label{width: 6em!important;
}
.space_detail .weui-cell__bd input::-webkit-input-placeholder{color: #999;font-size: 15px;
}
.space_detail .weui-cell__bd .weui-select{font-size: 15px;
}
.space_detail .weui-cell__bd option{color: #999;font-size: 15px;
}
.space_detail .weui-textarea{height: 120px!important;
}
.space_detail .weui-cells__title {margin-bottom: 0.77em;
}
.space_detail .weui-cells_radio .weui-cell__ft{padding-left:0;width: 30px;height: 30px;border:1px solid #ccc;border-radius: 50%;
}
.space_detail .weui-cells_radio .weui-check:checked + .weui-icon-checked:before{font-size: 21px;
}
.space_detail .weui-btn_primary{background-color:#5998F5 ;
}
.space_detail button.weui-btn{width: 94%;margin:10px auto;font-size: 15px;
}
.space_detail .year_paper{margin-bottom: 75px;padding-left: 20px;padding-top: 10px;
}
.space_detail .year_paper label{font-size: 13px;color:#999
}
.space_detail .year_paper label input{position: relative;top:1px
}
.space_detail .year_paper .agree{margin-right: 10px;
}
.space_detail input,.space_detail textarea{font-size: 14px;
}
.space_detail .btn{position:fixed;bottom:0px;height: 55px;background-color: #fff;width: 100%;border-top:1px solid #ededed
}



