body * {
  padding: 0;
  margin: 0;
}
body body,
body html {
  width: 100%;
  height: 100%;
  background: #f5f5f5;
}
body ul li {
  list-style: none;
}
body .flex_start {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}
body .flex_between {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
body .flex_around {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
body .flex_start_v {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
}
body .flex_between_v {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
}
body .flex_around_v {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
}
body .vertical_m {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
}
body .main_color {
  color: #6495ed;
}
body .main_fcolor {
  color: #333333;
}
body .minor_fcolor {
  color: #999999;
}
body .main_infoColor {
  color: #ff4a3c;
}
body .hr_color {
  color: #e5e5e5;
}
body .bg_color {
  color: #f5f5f5;
}
body .font_size14 {
  font-size: 14px;
}
body footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  height: 50px;
  /*padding: 0 10px;*/
  box-sizing: border-box;
  background: #fff;
  border-top: 1px solid #ddd;
}



