
html[data-v-f2807500],body[data-v-f2807500] {background: #f6fbf9
}
.mgr50[data-v-f2807500] {
  margin-right: 50px;
}
.no_footer[data-v-f2807500] {
  padding-top: 51px;
}
section[data-v-f2807500] {
  overflow: hidden;
}
/* 主体内容 */
.register_wrapper .register_content[data-v-f2807500]{margin:15px 0 36px;
}
.register_wrapper .register_item[data-v-f2807500]{display:-webkit-box;border-bottom:1px solid #e5e5e5;
}
.register_wrapper .register_item[data-v-f2807500]:first-child{border-top:1px solid #e5e5e5;
}
/* 图标 */
.register_wrapper .register_content p i[data-v-f2807500]{width:32px;display:block;
}
.register_wrapper .register_content_phone .register_content_phone_icon[data-v-f2807500]{background:url("../../../static/img/new_phone.png") no-repeat center #fff;background-size:70%;
}
.register_wrapper .register_content_password .register_content_password_icon[data-v-f2807500]{background:url("../../../static/img/suo.png") no-repeat center #fff;background-size:70%;
}
.register_wrapper .register_content_valid .register_content_valid_icon[data-v-f2807500]{background:url("../../../static/img/suo.png") no-repeat center #fff;background-size:70%;
}
.register_wrapper .register_content_valid .nosee[data-v-f2807500]{background:url("../../../static/img/eye.png") no-repeat center #fff;background-size:70%;
}
/* 输入框 */
.register_wrapper .register_content p input[data-v-f2807500]{outline: none
}
.register_wrapper .register_content p .register_content_phone_input[data-v-f2807500]{-webkit-box-flex:1;height:20px;display:block;padding:10px;border:none;
}
.register_wrapper .register_content p .register_content_password_input[data-v-f2807500]{-webkit-box-flex:1;height:20px;display:block;padding:10px;border:none;
}
.register_wrapper .register_content p .register_content_valid_input[data-v-f2807500]{-webkit-box-flex:1;height:20px;display:block;padding:10px;border:none;
}
/* 发送验证码、密码是否可见 */
.register_wrapper .register_content p .register_content_valid_btn[data-v-f2807500]{width:100px;line-height:40px;display:block;background:#fff;text-align:center;color: #6395ec;font-size: 14px;
}
.register_wrapper .register_content p .sent[data-v-f2807500]{width:100px;font-size:12px;line-height:40px;display:block;background:#fff;color:#6395ec;text-align:center;
}
.register_wrapper .register_content p .register_content_password_see[data-v-f2807500]{width:50px;display:block;background:url("../../../static/img/eye.png") no-repeat center #fff;background-size:40%;
}
.register_wrapper .register_content p .nosee[data-v-f2807500]{background:url("../../../static/img/eyes.png") no-repeat center #fff;background-size:40%;
}
.register_wrapper .register_content p.valid_hide[data-v-f2807500]{display: none;
}
/* 注册按钮 */
.register_wrapper .register_btn_wrap[data-v-f2807500]{display:-webkit-box;
}
.register_wrapper .register_btn_wrap .register_btn[data-v-f2807500]{border-radius:5px;margin:0 12px 15px;display:block;height:40px;line-height:40px;font-size:16px;-webkit-box-flex:1;background:#6395ec;color:#fff;text-align:center;
}
/* 服务协议 */
.register_wrapper .allow_protocol_wrap[data-v-f2807500]{color:#8a8a8a;text-align:center;
}
.register_wrapper .allow_protocol_wrap .protocol_link[data-v-f2807500]{text-decoration:underline;color:#6395ec;
}
/*------- 登录页面 结束 -------*/



