
header{height: 51px;background: #6395ec;color: #fff;font-size: 14px;position: fixed;width: 100%;top:0;z-index:100
}
header .header_left,.header_right{display: inline-block;width: 70px;height: 100%;line-height: 51px;text-align: center
}
header .header_left{margin-left: 10px; text-align: left;
}
header .header_right{margin-right: 10px; text-align: right;
}
header .header_middle{font-size: 16px;
}
header .header_left img{position: relative;top:4px;
}
header .header_right img{position: relative;top:5px;
}



